const CMFStunEffect = `using UnityEngine;

[CreateAssetMenu(menuName = "Attack Effects/Stun Effect")]
public class StunEffect : AttackEffect
{
    const int RangeMin = 0;
    const int RangeMax = 100;
    [Range(RangeMin, RangeMax)]
    public int stunChance;

    public override void ApplyEffect(Unit self, Unit opponent, bool criticalHit)
    {
        Unit unit = applyEffectToSelf ? self : opponent;
        const int RandomValueMultiplier = RangeMax;
        if (Random.value * RandomValueMultiplier <= stunChance)
        {
            unit.Stun();
        }
    }
}`;
export default CMFStunEffect;