const CMFRobot = `using System;
using System.Collections;
using System.Collections.Generic;
using UnityEngine;

public class Robot : MonoBehaviour
{
    private const int _NEW_BOT_COST = 1000;
    private const int _UPGRADE_COST = 200;
    private const float _UPGRADE_HP_MULTIPLIER = 0.2f;
    private const float _UPGRADE_DAMAGE_MULTIPLIER = 0.2f;
    public RobotData defaultData;
    private GameDataSaver<RobotData, RobotDataSerializable> _saver;

    public delegate void eventRobotDataSaved(Robot robot);
    public static event eventRobotDataSaved RobotDataSaved;

    private RobotData _playerData;
    public RobotData PlayerData
    {
        get
        {
            if (_playerData == null)
            {
                _playerData = Instantiate(defaultData);
                GetSaver().Load();
            }
            return _playerData;
        }
        private set => _playerData = value;
    }

    private void Awake()
    {
        // cloning/instatiating scriptable objects prevents modification of original objects
        defaultData = Instantiate(defaultData);
    }

    private GameDataSaver<RobotData, RobotDataSerializable> GetSaver()
    {
        if (_saver == null)
        {
            _saver = new GameDataSaver<RobotData, RobotDataSerializable>(PlayerData, defaultData.unitName);
        }
        return _saver;
    }

    public int GetCost()
    {
        if (PlayerData.ownedByPlayer)
        {
            int level = PlayerData.unitLevel;
            // formula for cost of upgrade
            int cost = _UPGRADE_COST + _UPGRADE_COST * (level - 1);
            return cost;
        }
        else
        {
            return _NEW_BOT_COST;
        }
    }

    public void BuyRobot()
    {
        PlayerData.ownedByPlayer = true;
        SaveCurrentData();
    }

    public void UpgradeRobot()
    {
        UpgradeRobotData(PlayerData);
        SaveCurrentData();
    }

    private void UpgradeRobotData(RobotData robotData)
    {
        robotData.unitLevel++;
        robotData.maxHp += Mathf.RoundToInt(defaultData.maxHp * _UPGRADE_HP_MULTIPLIER);
        robotData.damage += Mathf.RoundToInt(defaultData.damage * _UPGRADE_DAMAGE_MULTIPLIER);
    }

    public void SaveCurrentData()
    {
        GetSaver().Save();
        RobotDataSaved?.Invoke(this);
    }

    public RobotData CreateEnemyData()
    {
        BattleStats stats = new BattleStats();
        var statsSaver = new GameDataSaver<BattleStats, BattleStatsSerializable>(stats, SaveIds.BattleStats);
        statsSaver.Load();
        RobotData data = Instantiate(defaultData);
        const int STARTING_LEVEL = 1;
        const int WINS_PER_LEVEL = 2;
        const int LOSES_PER_LEVEL = 1;
        int levelIncrease = stats.WinCount / WINS_PER_LEVEL;
        int levelDecrease = stats.LoseCount / LOSES_PER_LEVEL;
        int level = STARTING_LEVEL + levelIncrease - levelDecrease;
        if (level < STARTING_LEVEL)
            level = STARTING_LEVEL;
        const int FIRST_UPGRADE_LEVEL = 2;
        for (int i = FIRST_UPGRADE_LEVEL; i <= level; i++)
        {
            UpgradeRobotData(data);
        }
        return data;
    }

    public bool Equals(Robot other)
    {
        return defaultData.unitName == other.defaultData.unitName;
    }
}`;
export default CMFRobot;