const RustbanditsProjectile = `using System;
using System.Collections;
using Audio;
using UnityEngine;
using Photon.Pun;

namespace Battle
{
    [DisallowMultipleComponent]
    class Projectile : MonoBehaviour, IHittable, IHitter
    {
        [SerializeField] private int speed = 1000;
        [SerializeField, Range(MinAccuracy, MaxAccuracy)] private float accuracy = 80f;
        [SerializeField] private float basicDamageMultiplier = 1.0f;
        private int direction = 0;
        private Rigidbody2D Rigidbody { get; set; }
        public Collider2D Collider { get; set; }

        public delegate void Hit(Collision2D collision, IHitter hitter);

        public event Hit OnHit;

        public PhotonView PhotonView { get; private set; }

        private bool destroyCalled = false;

        private const float MinAccuracy = 0f;
        private const float MaxAccuracy = 100f;
        private const float AccuracyYMultiplier = 5f;

        private bool isAI = false;

        public void SetDirection(Direction direction)
        {
            SetDirectionLocal((int)direction);
            PhotonView.RPC(nameof(SetDirectionRPC), RpcTarget.Others, (int)direction);
        }

        private void SetDirectionLocal(int direction)
        {
            this.direction = direction;
            transform.localScale = new Vector3
            {
                x = transform.localScale.x * -this.direction,
                y = transform.localScale.y,
                z = transform.localScale.z
            };
        }

        [PunRPC]
        private void SetDirectionRPC(int direction)
        {
            SetDirectionLocal(direction);
        }

        public void SetIsAI(bool isAI)
        {
            this.isAI = isAI;
            if (isAI)
            {
                gameObject.layer = BattleConfig.AIProjectileLayer;
            }
        }

        private void Awake()
        {
            Collider = GetComponent<Collider2D>();
            Rigidbody = GetComponent<Rigidbody2D>();
            PhotonView = GetComponent<PhotonView>();

            if (!PhotonView.IsMine)
            {
                Collider.enabled = false;
                Rigidbody.isKinematic = true;
            }
            else if (!isAI)
            {
                gameObject.layer = BattleConfig.MyProjectileLayer;
            }
        }

        private void Start()
        {
            if (!PhotonView.IsMine)
            {
                return;
            }

            if (direction == 0)
            {
                Debug.LogError("Projectile direction was not set");
                return;
            }

            Vector2 force = new Vector2
            {
                x = direction * speed,
                y = (MaxAccuracy - Mathf.Clamp(accuracy, MinAccuracy, MaxAccuracy)) * (UnityEngine.Random.value - 0.5f) * AccuracyYMultiplier
            };
            AudioManager.PlaySound(AudioManager.Sound.BasicAttack);
            Rigidbody.AddForce(force);
        }

        private void OnCollisionEnter2D(Collision2D collision)
        {
            if (!PhotonView.IsMine)
            {
                return;
            }
            collision.gameObject.GetComponent<IHittable>()?.GetHitBy(this);
            OnHit?.Invoke(collision, this);
            // There is an edge case when a projectile hits two targets during the same frame.
            // This will prevent an error like "Ev Destroy Failed. Could not find PhotonView with instantiationId...".
            if (destroyCalled)
            {
                return;
            }
            PhotonNetwork.Destroy(gameObject);
            destroyCalled = true;
        }

        public void GetHitBy(IHitter hitter)
        {
            if (!PhotonView.IsMine)
            {
                return;
            }
            PhotonNetwork.Destroy(gameObject);
        }

        public virtual float HitDamage => Player.State.Instance.GetCurrentUpgradeDamage() * basicDamageMultiplier;
    }
}
`;
export default RustbanditsProjectile;