const CovertConversation = `using System.Collections;
using UnityEngine;
using PixelCrushers.DialogueSystem;
using UnityEngine.Assertions;
using System.Linq;
using System.Collections.Generic;
using Mechanics.NPCMoods;

namespace Mechanics.Eavesdropping
{
    [RequireComponent(typeof(DaytimeSuspicionScript))]
    public class Conversation : MonoBehaviour
    {
        [SerializeField] public float suspicionIncreaseIfCaught;
        [SerializeField] public ConversationParticipant conversant1;
        [SerializeField] public ConversationParticipant conversant2;
        [SerializeField] public string conversationTitle;
        [SerializeField] private float _startingDelay = 0f;
        [SerializeField] public float breakBetweenConversations;
        [SerializeField] public bool isUnique = false;
        public bool IsEavesdropped { get; private set; } = false;
        public bool IsOver { get; set; } = false;
        public DaytimeSuspicionScript SuspicionScript { get; private set; }
        public Transform Eavesdropper { get; private set; }
        public int TimesStarted { get; set; } = 0;

        private void Awake()
        {
            SuspicionScript = GetComponent<DaytimeSuspicionScript>();
            Assert.IsFalse(suspicionIncreaseIfCaught == 0f);
            Assert.IsNotNull(conversant1);
            Assert.IsNotNull(conversant2);
            Assert.IsNotNull(conversationTitle);
            Assert.IsFalse(conversationTitle == string.Empty);
            Assert.IsNotNull(SuspicionScript);
            SuspicionScript.enabled = false;
        }

        private void Start()
        {
            ConversationManager.Instance.AddConversation(this, Time.time + _startingDelay);
        }

        public void SetIsEavesdropped(Transform eavesdropper)
        {
            IsEavesdropped = true;
            Eavesdropper = eavesdropper;
            SuspicionScript.enabled = true;
        }

        public void SetIsNotEavesdropped()
        {
            SuspicionScript.enabled = false;
            IsEavesdropped = false;
            Eavesdropper = null;
        }
    }
}`;
export default CovertConversation;