const CovertConversationManager = `using PixelCrushers.DialogueSystem;
using System.Collections;
using System.Collections.Generic;
using System.Linq;
using UnityEngine;
using UnityEngine.Assertions;

namespace Mechanics.Eavesdropping
{
    public class ConversationManager : MonoBehaviour
    {
        private static ConversationManager _instance;
        public static ConversationManager Instance
        {
            get
            {
                if (_instance is null)
                {
                    string errorMessage = $"The {nameof(Eavesdropping)}.{nameof(ConversationManager)} instance is null. ";
                    errorMessage += "Make sure there is a ConversationManager present on the scene.";
                    Debug.LogError(errorMessage);
                }
                return _instance;
            }
        }
        public static bool InstanceExists { get => _instance != null; }
        public Transform Player { get => _player; }
        [SerializeField] private Transform _player = null;
        public Eavesdropping Eavesdropping { get => _eavesdropping; }
        private Eavesdropping _eavesdropping;
        public List<RunningConversation> RunningConversations { get; private set; } = new List<RunningConversation>();
        public RestartableConversations RestartableConversations { get; private set; } = new RestartableConversations();
        private List<Conversation> _stoppedConversations = new List<Conversation>();
        private StandardDialogueUI StandardDialogueUI { get; set; }
        private RectTransform StandardDialogueUIRectTransform { get; set; }

        private const int _FRAMES_PER_UPDATE = 4;
        private bool DialoguePanelIsAboutToBecomeVisible { get; set; } = false;

        private void Awake()
        {
            _instance = this;
            _eavesdropping = new Eavesdropping(_instance);
            if (!DialogueManager.AllowSimultaneousConversations)
            {
                string _error = "\\"Allow Simultaneous Conversations\\" on the Dialogue Manager cannot be changed ";
                _error += "from code and needs to be manually set to true. You can find the checkbox in ";
                _error += "\\"Other Settings\\".";
                Debug.LogError(_error);
            }
            Assert.IsNotNull(_player);
        }

        private IEnumerator Start()
        {
            yield return null; // wait a frame because StandardDialogueUI isn't instantiated yet
            StandardDialogueUI = DialogueManager.Instance.gameObject.GetComponentInChildren<StandardDialogueUI>();
            StandardDialogueUIRectTransform = StandardDialogueUI.gameObject.GetComponent<RectTransform>();
            HideDialoguePanel();
        }

        private RunningConversation FindRunningConversation(Conversation conversation)
        {
            return RunningConversations
                .Where(c => c.conversation == conversation)
                .FirstOrDefault();
        }

        public void StopConversationForever(Conversation conversation)
        {
            conversation.IsOver = true;
            RunningConversation runningConversation = FindRunningConversation(conversation);
            if (runningConversation != null)
            {
                RunningConversations.Remove(runningConversation);
                ActiveConversationRecord activeConversationRecord = runningConversation.GetConversationRecord();
                if (activeConversationRecord != null)
                {
                    StartCoroutine(CloseConversation(activeConversationRecord.ConversationController));
                }
            }
            RemoveConversationFromConversationsToRestart(conversation);
        }

        private void RemoveConversationFromConversationsToRestart(Conversation conversation)
        {
            RestartableConversation restartableConversation = RestartableConversations
                .GetAll()
                .Where(c => c.conversation == conversation)
                .FirstOrDefault();
            if (restartableConversation.Equals(default(RestartableConversation)))
                return;
            RestartableConversations.Remove(restartableConversation);

        }

        public void StopAllRunningConversations(params RunningConversation[] except)
        {
            for (int i = 0; i < RunningConversations.Count; i++)
            {
                RunningConversation item = RunningConversations[i];
                if (except.Any(c => c == item))
                    continue; // skip the excluded conversations
                ActiveConversationRecord itemConversationRecord = item.GetConversationRecord();
                if (itemConversationRecord is null)
                {
                    // conversation has already ended
                    continue;
                }
                StartCoroutine(CloseConversation(itemConversationRecord.ConversationController));
                _stoppedConversations.Add(item.conversation);
                RunningConversations.RemoveAt(i--);
                // decrementing because we just removed an item and the next item took over its index
            }
        }

        private IEnumerator CloseConversation(ConversationController conversationController)
        {
            conversationController.Close();
            yield return new WaitUntil(() => DialogueManager.IsConversationActive);
            DialogueManager.SetDialoguePanel(show: true, immediate: true);
        }

        public void RestartStoppedConversations()
        {
            foreach (var item in _stoppedConversations)
            {
                RestartableConversations.Add(item, Time.time);
            }
            _stoppedConversations.Clear();
        }

        public void AddConversation(Conversation conversation, float startTime)
        {
            RestartableConversations.Add(conversation, startTime);
        }

        private void StartConversation(Conversation conversation)
        {
            Transform conversant1 = conversation.conversant1.transform;
            Transform conversant2 = conversation.conversant2.transform;
            DialogueManager.StartConversation(conversation.conversationTitle, conversant1, conversant2);
            RunningConversations.Add(new RunningConversation
            {
                id = DialogueManager.CurrentConversationState.subtitle.dialogueEntry.conversationID,
                conversation = conversation
            });
            if (conversation.IsEavesdropped)
            {
                StartCoroutine(ShowDialoguePanel());
            }
            else if (conversation.TimesStarted == 0)
            {
                HideDialoguePanel();
            }
            conversation.TimesStarted++;
        }

        public IEnumerator ShowDialoguePanel(bool setScale = true)
        {
            yield return new WaitUntil(() => DialogueManager.IsConversationActive);

            // SetDialoguePanel() apparently sometimes needs to be called twice
            // to become visible, so we call it in two consecutive frames

            DialoguePanelIsAboutToBecomeVisible = true;
            DialogueManager.SetDialoguePanel(show: true, immediate: true);
            yield return null;
            DialogueManager.SetDialoguePanel(show: true, immediate: true);
            DialoguePanelIsAboutToBecomeVisible = false;
            if (setScale)
            {
                SetDialoguePanelScale(Vector3.one);
            }
        }

        public void SetDialoguePanelScale(Vector3 scale)
        {
            StandardDialogueUIRectTransform.localScale = scale;
        }

        public void HideDialoguePanel()
        {
            /* The dialogue panel is never actually hidden, we just set its
             * scale to zero to avoid problems with Dialogue System's 
             * SetDialoguePanel() function.
             */
            SetDialoguePanelScale(Vector3.zero);
        }

        private void Update()
        {
            // run the following for-loops every x frames for better performance
            if (Time.frameCount % _FRAMES_PER_UPDATE != 0)
                return;

            RestartableConversation[] restartableConversations = RestartableConversations.GetAll();
            if (restartableConversations.Length != 0)
            {
                for (int i = 0; i < restartableConversations.Length; i++)
                {
                    if (restartableConversations[i].CanBeRestarted())
                    {
                        StartConversation(restartableConversations[i].conversation);
                        RestartableConversations.Remove(restartableConversations[i]);
                    }
                }
            }

            for (int i = 0; i < RunningConversations.Count; i++)
            {
                RunningConversation runningConversation = RunningConversations[i];
                ActiveConversationRecord activeConversation = runningConversation.GetConversationRecord();
                if (activeConversation is null)
                {
                    // conversation has ended
                    // let's schedule it for restart
                    RestartableConversations.Add(
                        runningConversation.conversation,
                        Time.time + runningConversation.conversation.breakBetweenConversations);
                    // also removing it form list of running conversations (it will be re-added when it starts again)
                    RunningConversations.RemoveAt(i--);
                    continue;
                }
                Subtitle currentSubtitle = activeConversation.ConversationController.currentState.subtitle;
                if (currentSubtitle.dialogueEntry.DialogueText == string.Empty)
                {
                    // dialogue text is empty so let's not bark
                    // (it's usually empty during the frame in which conversation has started)
                    continue;
                }
                if ((runningConversation.lastSubtitle is null || runningConversation.lastSubtitle != currentSubtitle)
                    && !runningConversation.conversation.IsEavesdropped)
                {
                    // barking here
                    DialogueManager.BarkString(
                        currentSubtitle.dialogueEntry.MenuText,
                        currentSubtitle.speakerInfo.transform
                    );
                    runningConversation.lastSubtitle = currentSubtitle;
                }
            }
        }
    }
}`;
export default CovertConversationManager;