const RustbanditsWall = `using System;
using System.Collections;
using Audio;
using UnityEngine;
using Battle.SkillSpawns;
using Core;
using Photon.Pun;

namespace Battle.Skills
{
    class Wall : Skill
    {
        [SerializeField] private float xOffset = 1;
        [SerializeField] private float yOffsetForEachSide = 1;
        [SerializeField] private WallSpawn wallSpawn;

        private int spawnInstances = 0;

        protected override void OnSkillInitialize()
        {
            base.OnSkillInitialize();
            AudioManager.PlaySound(AudioManager.Sound.Wall, transform.position);
            string prefab = Helpers.PrefabResourcePath(wallSpawn.gameObject, Config.SkillSpawnsFolder);

            float positionX = transform.position.x + (User.Side.DirectionAsInt * xOffset);

            Vector2[] positions =
            {
                new Vector2(positionX, transform.position.y + yOffsetForEachSide),
                new Vector2(positionX, transform.position.y),
                new Vector2(positionX, transform.position.y - yOffsetForEachSide)
            };

            float baseHPOfOwner = Player.State.Instance.GetCurrentUpgradeHP();
            for (int i = 0; i < positions.Length; i++)
            {
                GameObject goInstance = PhotonNetwork.Instantiate(prefab, positions[i], Quaternion.identity);
                WallSpawn wsInstance = goInstance.GetComponent<WallSpawn>();
                wsInstance.PhotonView.RPC(nameof(wsInstance.SetBaseHPOfOwner), RpcTarget.All, baseHPOfOwner);
                wsInstance.OnDestroyed += HandleSpawnDestroy;
                spawnInstances++;
            }
        }

        private void HandleSpawnDestroy()
        {
            if (--spawnInstances > 0)
            {
                return;
            }
            if (gameObject != null)
            {
                OnSkillOver();
            }
        }
    }
}
`;
export default RustbanditsWall;