const CMFBattleStats = `public class BattleStats : ISavableDataContainer<BattleStatsSerializable>
{
    public int WinCount { get; private set; } = 0;
    public int LoseCount { get; private set; } = 0;
    public int TieCount { get; private set; } = 0;

    /// <summary>
    /// Adds a single victory.
    /// </summary>
    public void AddWin()
    {
        WinCount++;
    }

    /// <summary>
    /// Adds a single defeat.
    /// </summary>
    public void AddLose()
    {
        LoseCount++;
    }

    /// <summary>
    /// Adds a single tie.
    /// </summary>
    public void AddTie()
    {
        TieCount++;
    }

    public BattleStatsSerializable GetSerializable()
    {
        return new BattleStatsSerializable
        {
            winCount = WinCount,
            loseCount = LoseCount,
            tieCount = TieCount
        };
    }

    public void Deserialize(BattleStatsSerializable serializable)
    {
        WinCount = serializable.winCount;
        LoseCount = serializable.loseCount;
        TieCount = serializable.tieCount;
    }
}`;
export default CMFBattleStats;