const RustbanditsAIDriver = `using System.Collections;
using UnityEngine;
using BehaviourTree;
using Tree = BehaviourTree.Tree;
using Battle.AITasks;
using InputSystem;

namespace Battle
{
    class AIDriver : MonoBehaviour
    {
        public AIDrivenInput Input { get; private set; } = new AIDrivenInput();
        private readonly Tree tree = new();
        private Unit otherUnit;

        private IEnumerator Start()
        {
            otherUnit = GetComponent<Unit>().OtherUnit;

            Sequence sequence = new("Main loop");
            AITask followOtherUnit = new Follow(this, otherUnit.transform);
            AITask shoot = new Shoot(this);
            AITask wait = new Wait(this, () => Random.value);
            Selector skillSelector = new("Skill selector");
            AITask randomSuccess = new RandomSuccess(this, probability: 0.925);
            AITask useRandomSkill = new UseRandomSkill(this);

            sequence.AddChild(new Leaf(nameof(Wait), wait.Run));
            sequence.AddChild(new Leaf(nameof(Follow), followOtherUnit.Run));
            skillSelector.AddChild(new Leaf(nameof(RandomSuccess), randomSuccess.Run));
            skillSelector.AddChild(new Leaf(nameof(UseRandomSkill), useRandomSkill.Run));
            sequence.AddChild(skillSelector);
            sequence.AddChild(new Leaf(nameof(Shoot), shoot.Run));

            tree.AddChild(sequence);

            while (true)
            {
                Node.Status status = tree.Process();
                yield return null;
            }
        }
    }
}`;
export default RustbanditsAIDriver;