const CometCollectible = `using System.Collections;
using UnityEngine;

[System.Serializable]
public abstract class Collectible
{
    [SerializeField] public CollectibleBehaviour prefab;
    [SerializeField] public Material material;
    [SerializeField] public Color textColor;

    [Range(0.1f, 20f)]
    public float speed;

    [Header("Min and max number of seconds between spawns:")]
    public float minSpawn;
    public float maxSpawn = 10f;

    public abstract float GetMassChangeOnCollect(float currentMass);
    public abstract string GetDisplayText();
}`;
export default CometCollectible;