const CometCometMass = `using System.Collections;
using System.Collections.Generic;
using UnityEngine;

public class CometMass : MonoBehaviour
{
    [SerializeField] private int minMass = 400;
    [SerializeField] private int maxMass = 600;
    public int MinMass => minMass;
    public int MaxMass => maxMass;

    private float currentMass;
    public float CurrentMass
    {
        get => currentMass;
        set
        {
            currentMass = Mathf.Clamp(value, minMass, maxMass);
            Changed?.Invoke(currentMass);
        }
    }
    public float CurrentMass01 => Mathf.InverseLerp(minMass, maxMass, currentMass);

    public delegate void OnChanged(float newMass);
    public static event OnChanged Changed;

    private void Start()
    {
        CurrentMass = minMass;
    }

    public void AddMass(int mass)
    {
        CurrentMass += mass;
    }

    public void SubtractMass(int mass)
    {
        CurrentMass -= mass;
    }

    public void HandleCollectible(CollectibleBehaviour collectible)
    {
        AddMass((int)collectible.collectible.GetMassChangeOnCollect(CurrentMass));
    }
}`;
export default CometCometMass;