const CovertDrinkingConversation = `using System.Collections;
using UnityEngine;
using PixelCrushers.DialogueSystem;

namespace Mechanics.Drinking
{
    [RequireComponent(typeof(DialogueSystemTrigger))]
    public class DrinkingConversation : MonoBehaviour
    {
        private const int _ID_UNSET = -1;
        [SerializeField] private DialogueSystemTrigger _dialogueSystemTrigger = null;
        [SerializeField] private int _uniqueNpcId = _ID_UNSET;
        [SerializeField] private float _suspicionValue = default;
        public int Drunkenness { get; private set; }
        public int MaxDrunkenness { get; } = 100;
        public int Suspicion { get; private set; }
        public int MaxSuspicion { get; } = 100;

        public delegate void eventDrunkennessHasChanged(int newDrunkenness);
        public event eventDrunkennessHasChanged DrunkennessHasChanged;

        public delegate void eventSuspicionHasChanged(int newSuspicion);
        public event eventSuspicionHasChanged SuspicionHasChanged;

        private bool _manorSuspicionPenaltyApplied = false;

        private void OnValidate()
        {
            _dialogueSystemTrigger = GetComponent<DialogueSystemTrigger>();
            if (_dialogueSystemTrigger != null)
            {
                _dialogueSystemTrigger.trigger = DialogueSystemTriggerEvent.OnUse;
            }
            if (_uniqueNpcId < 0)
            {
                string message = $"{nameof(DrinkingConversation)}: NPC id must be unique and non-negative!";
                Debug.LogError(message);
            }
        }

        /// <summary>
        /// Call this function to start the conversation.
        /// </summary>
        public void StartConversation()
        {
            string luaCondition = nameof(GetNpcId) + " ~= nil";
            if (Lua.IsTrue(luaCondition))
            {
                UnregisterLuaFunctions();
            }
            RegisterLuaFunctions();

            _dialogueSystemTrigger.OnUse();
            Core.CursorHelper.ShowCursor();
        }

        /// <summary>
        /// Dialogue system message. Do not call this manually.
        /// </summary>
        public void OnConversationEnd()
        {
            UpdateValuesFromLua();
            UnregisterLuaFunctions();
            Core.CursorHelper.HideCursor();
        }

        public void OnConversationLine(Subtitle _)
        {
            UpdateValuesFromLua();
        }

        private void RegisterLuaFunctions()
        {
            Lua.RegisterFunction(nameof(GetNpcId), this, SymbolExtensions.GetMethodInfo(() => GetNpcId()));
        }

        private void UnregisterLuaFunctions()
        {
            Lua.UnregisterFunction(nameof(GetNpcId));
        }

        private void UpdateValuesFromLua()
        {
            const string GET_DRUNKENNESS = "return Variable[drunkenness]";
            const string GET_SUSPICION = "return Variable[suspicion]";

            SetDrunkenness(Lua.Run(GET_DRUNKENNESS).AsInt);
            SetSuspicion(Lua.Run(GET_SUSPICION).AsInt);
        }

        private void SetDrunkenness(int drunkenness)
        {
            if (drunkenness != Drunkenness)
            {
                Drunkenness = drunkenness;
                DrunkennessHasChanged?.Invoke(drunkenness);
            }
        }

        private void SetSuspicion(int suspicion)
        {
            if (suspicion != Suspicion)
            {
                Suspicion = suspicion;
                SuspicionHasChanged?.Invoke(suspicion);

                if (Suspicion == MaxSuspicion)
                    ApplyManorSuspicionPenalty();
            }
        }

        private double GetNpcId()
        {
            return _uniqueNpcId;
        }

        private void ApplyManorSuspicionPenalty()
        {
            if (_manorSuspicionPenaltyApplied)
                return;

            if (Suspicion != MaxSuspicion)
                return;

            const int SUSPICION_LEVEL_ADDITION = 1;
            int multiplier = SUSPICION_LEVEL_ADDITION + ManorMoods.Suspicion.Instance.GetLevelNumeric();
            ManorMoods.Suspicion.Instance.IncreaseBy(_suspicionValue * multiplier);
            _manorSuspicionPenaltyApplied = true;
        }
    }
}`;
export default CovertDrinkingConversation;