const CometCollectibleMeteorite = `using System.Collections;
using UnityEngine;

[System.Serializable]
public class CollectibleMeteorite : Collectible
{
    [Header("Meteorite-specific settings:")]
    public int mass;

    public override string GetDisplayText()
    {
        return (mass >= 0 ? "+" : string.Empty) + mass + GameSettings.Instance.GameSettingsScriptableObject.massUnit;
    }

    public override float GetMassChangeOnCollect(float currentMass)
    {
        if (currentMass > mass)
        {
            return mass;
        }
        const float OneHundredPercent = 100f;
        float massLoss = currentMass * GameSettings.Instance.GameSettingsScriptableObject.massLossPercentageOnCrash / OneHundredPercent;
        return -(int)massLoss;
    }
}`;
export default CometCollectibleMeteorite;