const RustbanditsAnimationsArena = `using UnityEngine;
using System.Collections;
using Battle;
using Photon.Pun;

namespace Player
{
    class AnimationsArena : MonoBehaviour
    {
        private Animator animator = null;

        private readonly int isRunningBool = Animator.StringToHash("IsRun");
        private readonly int isShootingBool = Animator.StringToHash("IsShoot");
        private readonly int isDeadBool = Animator.StringToHash("IsDead");
        private readonly int[] isHitTrigger = new int[]
        {
            Animator.StringToHash("IsHit01"),
            Animator.StringToHash("IsHit02"),
            Animator.StringToHash("IsHit03")
        };

        private System.Random random = new();

        private Vector2 lastPosition;
        bool moved = false;

        private PhotonView view;

        // delay movement check by one frame by default
        private WaitForSeconds movementCheckReturn = null;

        private void Awake()
        {
            animator = GetComponent<Animator>();
            view = GetComponent<PhotonView>();
            lastPosition = transform.position;
            if (!view.IsMine)
            {
                movementCheckReturn = new WaitForSeconds(0.1f);
            }
        }

        private IEnumerator Start()
        {
            while (true)
            {
                yield return movementCheckReturn;
                animator.SetBool(isRunningBool, moved);
                moved = false;
            }
        }

        private void Update()
        {
            if (lastPosition != (Vector2)transform.position)
            {
                moved = true;
            }
            lastPosition = transform.position;
        }

        [PunRPC]
        public void PlayDamageAnimation()
        {
            animator.SetTrigger(isHitTrigger[random.Next(0, 3)]);
        }

        [PunRPC]
        public void SetFiringAnimation(bool isFiring)
        {
            animator.SetBool(isShootingBool, isFiring);
        }

        [PunRPC]
        public void PlayDeathAnimation()
        {
            animator.SetBool(isDeadBool, true);
        }
    }
}`;
export default RustbanditsAnimationsArena;