import React, { useState } from "react";
import Typography from "@material-ui/core/Typography";
import Project from "./Project";
import { red, blue, green, grey } from "@material-ui/core/colors";
import Masonry from "react-masonry-css";
import Link from '@material-ui/core/Link';

import { scrollTo, makeProjectId } from "./HelperFunctions";

// import Stack from '@mui/material/Stack';

// icons
// import SortIcon from '@material-ui/icons/Sort';

// radio
// import Radio from '@material-ui/core/Radio';
// import RadioGroup from '@material-ui/core/RadioGroup';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import FormControl from '@material-ui/core/FormControl';

// game icons
import icon_cmf from "./img/icon_cmf.png";
import icon_alien_rush from "./img/icon_alien_rush.png";
import icon_ld48 from "./img/icon_ld48.png";
import icon_arduino from "./img/icon_arduino.png";
import icon_final_road from "./img/icon_final_road.jpg";
import icon_covert from "./img/icon_covert.jpg";
import icon_comet from "./img/icon_comet.png";
import icon_rustbandits from "./img/icon_rustbandits.png";

// screenshots
import cmf1 from "./img/cmf1.jpg";
import cmf2 from "./img/cmf2.jpg";
import cmf3 from "./img/cmf3.jpg";
import cmf4 from "./img/cmf4.jpg";
import arduino1 from "./img/arduino1.png";
import ss_covert1 from "./img/ss_covert.jpg";
import ss_covert2 from "./img/ss_covert2.jpg";
import ss_covert3 from "./img/ss_covert3.jpg";
import ss_covert4 from "./img/ss_covert4.jpg";
import ss_alien_rush from "./img/ss_alien_rush.jpg";
import ss_ld48 from "./img/ss_ld48.jpg";
import ss_element_hunt from "./img/ss_element_hunt.png";
import final_road1 from "./img/final_road1.jpg";
import final_road2 from "./img/final_road2.jpg";
import rustbandits1 from "./img/ss_rustbandits1.jpg";
import rustbandits2 from "./img/ss_rustbandits2.jpg";
import { makeStyles } from "@material-ui/core";

//videos
import ArduinoGame1 from "./video/ArduinoGame1.webm";
import CometGame from "./video/CometGame.webm";
import Rustbandits from "./video/Rustbandits.webm";

// code samples
import CMFPlayerController from "./samples/CMFPlayerController";
import CMFBattleStats from "./samples/CMFBattleStats";
import CMFBattleStatsSerializable from "./samples/CMFBattleStatsSerializable";
import CMFISavableDataContainer from "./samples/CMFISavableDataContainer";
import CMFRobot from "./samples/CMFRobot";
import CMFStunEffect from "./samples/CMFStunEffect";
import RustbanditsProjectile from "./samples/RustbanditsProjectile";
import RustbanditsWall from "./samples/RustbanditsWall";
import RustbanditsFollow from "./samples/RustbanditsFollow";
import RustbanditsAIDriver from "./samples/RustbanditsAIDriver";
import RustbanditsHP from "./samples/RustbanditsHP";
import RustbanditsAnimationsArena from "./samples/RustbanditsAnimationsArena";
import OTPlayerNavigation from "./samples/OTPlayerNavigation";
import OTPlayerRespawn from "./samples/OTPlayerRespawn";
import OTPickupableObject from "./samples/OTPickupableObject";
import CometCollectible from "./samples/CometCollectible";
import CometCollectibleMeteorite from "./samples/CometCollectibleMeteorite";
import CometLevelSettingsScriptableObject from "./samples/CometLevelSettingsScriptableObject";
import CometGameProgressManager from "./samples/CometGameProgressManager";
import CometCometMass from "./samples/CometCometMass";
import CovertDrinkingConversation from "./samples/CovertDrinkingConversation";
import CovertConversation from "./samples/CovertConversation";
import CovertConversationManager from "./samples/CovertConversationManager";
import ArduinoPlayerCpp from "./samples/ArduinoPlayerCpp";
import ArduinoPlayerH from "./samples/ArduinoPlayerH";
import ArduinoGameCpp from "./samples/ArduinoGameCpp";
import ArduinoGameH from "./samples/ArduinoGameH";
import ArduinoWorldCpp from "./samples/ArduinoWorldCpp";
import ArduinoWorldH from "./samples/ArduinoWorldH";

const useStyles = makeStyles((theme) => ({
  masonryGrid: {
    display: "flex",
    marginLeft: theme.spacing(-4),
    width: "inherit"
  },
  masonryColumn: {
    paddingLeft: theme.spacing(4),
    backgroundClip: "padding-box",
    "& > div": {
      marginBottom: "30px"
    }
  }
}));

function Projects(props) {
  const classes = useStyles();
  const [order, setOrder] = useState("chronoDesc")

  let o = 0

  let projects = [
    { // Crazy Metal Fighters
      recommendedOrder: o,
      chronoOrder: 20200715,
      misc: false,
      listName: "(2020) \"Crazy Metal Fighters\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Crazy Metal Fighters"
          subheader="2020-03&thinsp;&ndash;&thinsp;2020-07, Lead programmer"
          iconBackground={red[500]}
          iconSrc={icon_cmf}
          screenshots={[cmf1, cmf2, cmf3, cmf4]}
          codeSamples={[
            ["PlayerController", CMFPlayerController],
            ["Robot", CMFRobot],
            ["StunEffect", CMFStunEffect],
            ["BattleStats", CMFBattleStats],
            ["BattleStatsSerializable", CMFBattleStatsSerializable],
            ["ISavableDataContainer", CMFISavableDataContainer],
          ]}
          colorA="#734429"
          colorB="#320A59"
          description="Crazy Metal Fighters is a casual turn-based strategy game for Android and Windows."
          itchUrl="https://crazymetalfighters.itch.io/crazy-metal-fighters"
          presskitUrl="https://bgalinski5.wixsite.com/crazymetalfighters"
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Micha Znajkiewicz, Ihor Yusko",
              "Artist: Marek Laufer",
              "Designer: Bartosz Galiski"
            ],
            myResponsibilities: [
              "Programming the battle system: turns, attacks, status effects and more",
              "Programming bot upgrades and enemy leveling",
              "Programming a solution for saving player's progress",
              "Customizing the toon shader",
              "Importing 3D models, textures and animations, programming the animations"
            ],
            techniquesEngine: ["Scriptable objects", "URP & Shader Graph"],
            techniquesLanguage: [
              "Delegates",
              "Generic classes",
              "Interfaces",
              "Inheritance",
              "Namespaces",
              "LINQ",
              "Singleton pattern"
            ]
          }}
        />
    },
    { // Rustbandits
      recommendedOrder: o,
      chronoOrder: 20220628,
      misc: false,
      listName: "(2022) \"Rustbandits\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Rustbandits"
          subheader="2022-03&thinsp;&ndash;&thinsp;2022-06"
          iconBackground={red[500]}
          iconSrc={icon_rustbandits}
          screenshots={[rustbandits1, rustbandits2]}
          videos={[Rustbandits]}
          codeSamples={[
            ["Battle.Projectile", RustbanditsProjectile],
            ["Battle.Skills.Wall", RustbanditsWall],
            ["Battle.AITasks.Follow", RustbanditsFollow],
            ["Battle.AIDriver", RustbanditsAIDriver],
            ["Battle.HP", RustbanditsHP],
            ["Player.AnimationsArena", RustbanditsAnimationsArena],
          ]}
          colorA="#734429"
          colorB="#320A59"
          description="Rustbandits is an online PVP game with top-down perspective pixel art graphics and optional single player mode."
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Micha Znajkiewicz",
              "Artists: Marek Laufer, Oleksii Bezpalyi",
            ],
            myResponsibilities: [
              "Programming the networking features (using Photon Unity Networking)",
              "Programming enemy AI with behaviour trees (for single-player mode)",
              "Programming skills in accordance with the GDD",
              "Programming animations",
              "Programming the use of emotes",
              "Programming battle UI",
              "Programming battle controls",
              "Fixing/finishing the shop (task taken over from other team member who couldn't handle it)"
            ],
          }}
        />
    },
    { // Arduino game
      recommendedOrder: o,
      chronoOrder: 20220215,
      misc: false,
      listName: "(2022) Arduino game / C++ (no game engine used)",
      project:
        <Project
          key={o++}
          projectName="Arduino game"
          subheader="2021-12&thinsp;&ndash;&thinsp;2022-02"
          iconBackground={grey[100]}
          iconSrc={icon_arduino}
          screenshots={[arduino1]}
          videos={[ArduinoGame1]}
          codeSampleLanguage="cpp"
          codeSamples={[
            ["Player.cpp", ArduinoPlayerCpp],
            ["Player.h", ArduinoPlayerH],
            ["Game.cpp", ArduinoGameCpp],
            ["Game.h", ArduinoGameH],
            ["World.cpp", ArduinoWorldCpp],
            ["World.h", ArduinoWorldH],
          ]}
          colorA="#734429"
          colorB="#320A59"
          description="Game about a frog that eats butterflies. On each playthrough, you are placed in a world with randomly placed butterflies and grass. Find and eat 5 butterflies to win."
          details={{
            team: [
              "Programming: Arkadiusz Bergmann",
              "Art, providing electronics, soldering etc.: Marek Laufer",
            ],
            language: "C++",
            engine: "None (game written from scratch, only two core libraries were used: one to handle the TFT display and another to draw pixels, shapes and text on the display)",
            myResponsibilities: [
              "The entirety of coding for this project, i.e.:",
              "Programming the core of the game",
              "Programming the rendering of sprites",
              "Programming the character movement",
              "Programming the animations",
              "Programming the detection of when the character picks up the butterfly",
              "Programming the world generation (random placement of sprites for each playthrough)",
              "Programming the LED diodes",
            ],
          }}
        />
    },
    { // The Door Below
      recommendedOrder: o,
      chronoOrder: 99990001,
      misc: false,
      listName: "(2021-2023) \"The Door Below\" / Unreal Engine 5 (ported from UE4 project)",
      project:
        <Project
          key={o++}
          projectName="The Door Below"
          subheader="2021-11&thinsp;&ndash;&thinsp;2023"
          iconBackground={green[200]}
          iconSrc={null}
          colorA="#734429"
          colorB="#320A59"
          description="A first person horror game."
          codeSampleLanguage="cpp"
          // codeSamples={[
          //   ["Door", "https://blueprintue.com/render/_b7hm0a0/", true]
          // ]}
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Arkadiusz awniczak, Jan Maciejewski",
            ],
            engine: "Unreal Engine 5 (project converted from UE4)",
            language: "visual scripting (Blueprints) and a bit of C++"
          }}
        />
    },
    { // Alien Rush
      recommendedOrder: o,
      chronoOrder: 20210415,
      misc: false,
      listName: "(2021) \"Alien Rush\" - MageJam vol. 4 game / Unity",
      project:
        <Project
          key={o++}
          projectName="Alien Rush (MageJam vol. 4 game)"
          subheader="2021-03&thinsp;&ndash;&thinsp;2021-04"
          iconBackground={blue[200]}
          iconSrc={icon_alien_rush}
          screenshots={[ss_alien_rush]}
          description="Alien Rush is a tower defense game for Windows set on a distant planet, where you will fight for survival of your race as enemies try to flood your base."
          itchUrl="https://arekb.itch.io/alien-rush"
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Arkadiusz awniczak, Jan Maciejewski"
            ],
          }}
        />
    },
    { // Covert
      recommendedOrder: o,
      chronoOrder: 20210215,
      misc: false,
      listName: "(2020-2021) \"Covert\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Covert"
          subheader="2020-10&thinsp;&ndash;&thinsp;2021-02"
          iconBackground={red[300]}
          iconSrc={icon_covert}
          screenshots={[ss_covert1, ss_covert2, ss_covert3, ss_covert4]}
          description="Covert is a 3D action-adventure game for Windows set in a world of anthropomorphic animals in the middle of a war similar to World War II."
          itchUrl=""
          codeSamples={[
            ["Mechanics.Eavesdropping.Conversation", CovertConversation],
            ["Mechanics.Eavesdropping.ConversationManager", CovertConversationManager],
            ["Mechanics.Drinking.DrinkingConversation", CovertDrinkingConversation],
          ]}
          details={{
            language: "C# and a bit of Lua",
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Arkadiusz awniczak, Jan Maciejewski",
              "Designer: Bartosz Galiski"
            ],
            myResponsibilities: [
              "Implementing the Dialogue System asset",
              "Programming the eavesdropping mechanic and the suspicion mechanic",
              "Programming the effects of drunkenness and suspicion during dialogues",
              "Programming the notepad and the machanic of gathering pieces of information",
              "Programming parts of the UI related to the mechanics above",
              "Implementing and adjusting the save system I already programmed for the previous project I worked on",
              "Programming menus (main menu, pause menu, settings menu)",
            ],
            techniquesEngine: [
              "Implementing an external asset (Dialogue System) that uses Lua",
              "Assembly definitions",
              "Scriptable objects",
            ],
            techniquesLanguage: [
              "Delegates",
              "Generic classes",
              "Abstract classes",
              "Namespaces",
              "LINQ (sparingly)",
              "Singleton pattern",
              "Getters and setters",
            ],
          }}
        />
    },
    { // LD48 game
      recommendedOrder: o,
      chronoOrder: 20210426,
      misc: false,
      listName: "(2021) Ludum Dare 48 game / Unity",
      project:
        <Project
          key={o++}
          projectName="Thrown Out (aka Wander through the caves in search of a new nest / Ludum Dare 48 game)"
          subheader="2021-04-24&thinsp;&ndash;&thinsp;2021-04-26"
          iconBackground={blue[100]}
          iconSrc={icon_ld48}
          screenshots={[ss_ld48]}
          description={`This is a Windows adventure game made for LD48.`}
          itchUrl="https://arekb.itch.io/wander-through-the-caves-in-search-of-a-new-nest"
          customUrl={["Ludum Dare", "https://ldjam.com/events/ludum-dare/48/wander-through-the-caves-in-search-of-a-new-nest"]}
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Jan Maciejewski, Arkadiusz awniczak"
            ],
          }}
        />
    },
    { // Old Tale
      recommendedOrder: o,
      chronoOrder: 20210630,
      misc: false,
      listName: "(2021) \"Old Tale\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Old Tale"
          subheader="2021-03&thinsp;&ndash;&thinsp;2021-06"
          iconBackground={green[100]}
          description={`A point'n'click adventure game with retro pixel graphics.`}
          itchUrl="https://darg8.itch.io/old-tale"
          codeSamples={[
            ["PlayerNavigation", OTPlayerNavigation],
            ["PlayerRespawn", OTPlayerRespawn],
            ["PickupableObject", OTPickupableObject],
          ]}
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Jan Maciejewski, Arkadiusz awniczak"
            ],
          }}
        />
    },
    { // Comet
      recommendedOrder: o,
      chronoOrder: 20211110,
      misc: false,
      listName: "(2021) \"Comet\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Comet"
          subheader="2021-10-08&thinsp;&ndash;&thinsp;2021-11-10"
          iconBackground={grey[900]}
          iconSrc={icon_comet}
          description={`A hyper-casual game for Android with multiple stages.`}
          videos={[CometGame]}
          thinRightPane
          codeSamples={[
            ["Collectible", CometCollectible],
            ["CollectibleMeteorite", CometCollectibleMeteorite],
            ["LevelSettingsScriptableObject", CometLevelSettingsScriptableObject],
            ["GameProgressManager", CometGameProgressManager],
            ["CometMass", CometCometMass],
          ]}
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Roman Nemchuk",
              "Artist: Eduard Nesterenko",
              "Designer: Adam Heinrich"
            ],
          }}
        />
    },
    { // Element Hunt
      recommendedOrder: o,
      chronoOrder: 20200215,
      misc: true,
      listName: "(2019-2020) \"Element Hunt\" / Unity",
      project:
        <Project
          key={o++}
          projectName="Element Hunt"
          subheader="2019-10&thinsp;&ndash;&thinsp;2020-02"
          iconBackground={blue[400]}
          iconSrc={null}
          screenshots={[ss_element_hunt]}
          description="Element Hunt is a Windows platformer game about a shaman, in which you embark on a journey to obtain the fourth element, but on the way encounter guardians who attempt to stop you."
          itchUrl="https://thegreenwolffe.itch.io/element-hunt"
          details={{
            team: [
              "Lead programmer: Aleksander Jaronik",
              "Programmers: Arkadiusz Bergmann, Ihor Yusko",
              "Artist: Marek Laufer",
              "Designer: Adam Heinrich"
            ],
          }}
        />
    },
    { // Final Road
      recommendedOrder: o,
      chronoOrder: 20210615,
      misc: false,
      listName: "(2021) \"Final Road\" / Unreal Engine 4",
      project:
        <Project
          key={o++}
          projectName="Final Road"
          subheader="2021-03&thinsp;&ndash;&thinsp;2021-06"
          iconBackground={green[200]}
          iconSrc={icon_final_road}
          screenshots={[final_road1, final_road2]}
          colorA="#734429"
          colorB="#320A59"
          description="Final Road is an action game about a lone adventurer venturing into an abandoned research facility."
          itchUrl="https://darg8.itch.io/final-road"
          details={{
            team: [
              "Programmers: Arkadiusz Bergmann, Jan Maciejewski",
              "Artists: Marek Laufer, Arkadiusz awniczak",
              "Designer: Bartosz Galiski"
            ],
            engine: "Unreal Engine 4",
            language: "visual scripting (Blueprints)"
          }}
        />
    },
  ]

  let p = 0;
  const projectCrazyMetalFighters = projects[p++];
  const projectArduinoGame = projects[p++];
  const projectTheDoorBelow = projects[p++];
  const projectAlienRush = projects[p++];
  const projectCovert = projects[p++];
  const projectLD48 = projects[p++];
  const projectOldTale = projects[p++];
  const projectComet = projects[p++];
  const projectElementHunt = projects[p++];
  const projectFinalRoad = projects[p++];

  projects = [
    projectCrazyMetalFighters,
    projectArduinoGame,
    projectTheDoorBelow,
    projectAlienRush,
    projectCovert,
    projectLD48,
    projectOldTale,
    projectComet,
    projectElementHunt,
    projectFinalRoad,
  ]

  projects.sort((a, b) => {
    switch (order) {
      case "recommended":
        return 0
      case "chronoDesc":
        return a.chronoOrder < b.chronoOrder ? 1 : -1
      case "chronoAsc":
        return a.chronoOrder > b.chronoOrder ? 1 : -1
    }
  })

  return (
    <React.Fragment>
      <Typography variant="h4" id="Projects" paragraph>
        Projects
      </Typography>
      <Typography variant="h6">
        Games
      </Typography>
      <ul>
        {projects.map(p => (
          <li><Link href="#" color="textSecondary" onClick={(e) => {
            e.preventDefault();
            scrollTo(makeProjectId(p.project.props.projectName));
          }}>{p.listName ? p.listName : p.project.props.projectName}</Link></li>
        ))}
      </ul>
      <Typography variant="h6">
        Other
      </Typography>
      {/* <ul>
          <li><Link href="#" color="textSecondary" onClick={(e) => {
            e.preventDefault();
            scrollTo(makeProjectId("Arduino demoscene project"));
          }}>{"(11-2022 to 01-2023) Arduino demoscene project / C++"}</Link></li>
          <li><Link href="#" color="textSecondary" onClick={(e) => {
            e.preventDefault();
            scrollTo(makeProjectId("Motion graphics intro for Trap City"));
          }}>{"(2013) Motion graphics intro for a world-famous YouTube channel I made at age 15"}</Link></li>
      </ul> */}
      <ul>
          <li>(2022-11 to 2023-01) Arduino demoscene project / C++</li>
          <li>(2022-06) Machine learning project - "Predict YouTube category from comments" / Python</li>
          <li>(2022-04 to 2022-05) Machine learning project - implementation of reinforcement learning in Unity</li>
      </ul>
      <br/><br/>
      {/* <Stack spacing={1} direction="row">
        <Typography style={{display: "flex", color: "rgba(255, 255, 255, 0.7)"}}><SortIcon fontSize="large" style={{alignSelf: "baseline"}} /></Typography>
        <Typography style={{display: "flex", color: "rgba(255, 255, 255, 0.7)"}}><span style={{alignSelf: "center"}}>Order: </span></Typography>
      </Stack>
      <Stack spacing={1} direction="row">
        <FormControl component="fieldset">
          <RadioGroup row aria-label="order" name="order1" value={order} onChange={(_, v) => setOrder(v)}>
            <FormControlLabel value="recommended" control={<Radio size="small" />} label="Default (recommended)" />
            <FormControlLabel value="chronoDesc" control={<Radio size="small" />} label="Chronological descending" />
            <FormControlLabel value="chronoAsc" control={<Radio size="small" />} label="Chronological ascending" />
          </RadioGroup>
        </FormControl>
      </Stack>
      <br/> */}
      <Masonry
        //breakpointCols={{ default: 2, 959: 1 }}
        breakpointCols={{ default: 1 }}
        className={classes.masonryGrid}
        columnClassName={classes.masonryColumn}
      >
        {projects.map(p => p.project)}
      </Masonry>
      <br />
    </React.Fragment>
  );
}

export default Projects;
