const RustbanditsFollow = `using BehaviourTree;
using UnityEngine;

namespace Battle.AITasks
{
    class Follow : AITask
    {
        private Transform target;
        private float proximityThreshold;

        public Follow(AIDriver driver, Transform target, float proximityThreshold = 0.2f) : base(driver)
        {
            this.target = target;
            this.proximityThreshold = proximityThreshold;
        }

        public override Node.Status Run()
        {
            if (Approximately(driver.transform.position.y, target.transform.position.y, proximityThreshold))
            {
                driver.Input.PressVerticalZero();
                return Node.Status.SUCCESS;
            }
            else if (driver.transform.position.y < target.transform.position.y)
            {
                driver.Input.PressVerticalPositive();
                return Node.Status.RUNNING;
            }
            else if (driver.transform.position.y > target.transform.position.y)
            {
                driver.Input.PressVerticalNegative();
                return Node.Status.RUNNING;
            }
            else
            {
                throw new System.InvalidOperationException();
            }
        }
    }
}`;
export default RustbanditsFollow;