import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { scrollTo, getScrollbarWidth, makeProjectId } from "./HelperFunctions";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import Stack from '@mui/material/Stack';
import Typography from "@material-ui/core/Typography";
import Box from '@material-ui/core/Box';
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faItchIo } from "@fortawesome/free-brands-svg-icons";
import NewspaperIcon from '@mui/icons-material/Newspaper';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import WebIcon from '@material-ui/icons/Web';
import { Light as SyntaxHighlighter } from 'react-syntax-highlighter';
import csharp from 'react-syntax-highlighter/dist/esm/languages/hljs/csharp';
import cpp from 'react-syntax-highlighter/dist/esm/languages/hljs/cpp';
import vs2015 from 'react-syntax-highlighter/dist/esm/styles/hljs/vs2015';

SyntaxHighlighter.registerLanguage('csharp', csharp);
SyntaxHighlighter.registerLanguage('cpp', cpp);

const defaultData = {
  engine: "Unity",
  language: "C#",
  sourceControl: "git"
};

const useStyles = makeStyles((theme) => ({
  cardRoot: (props) => ({
    backgroundColor: "#013B66",
    background: "linear-gradient(-45deg, #045f77, #013B66)"
  }),
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  expand: {
    // transform: "rotate(0deg)",
    // marginLeft: "auto",
    // transition: theme.transitions.create("transform", {
    //   duration: theme.transitions.duration.shortest
    // })
  },
  expandOpen: {
    // transform: "rotate(180deg)"
  },
  avatar: (props) => ({
    backgroundColor: props.iconBackground
  }),
  dividerMargin: {
    marginTop: "30px",
    marginBottom: "18px"
  },
  button: {
    margin: theme.spacing(1)
  },
  watermark: {
    position: "absolute",
    top: "56px", // tabs (48px) + 8px
    right: (16 + getScrollbarWidth()) + "px",
    opacity: .2,
    userSelect: "none",
    zIndex: 999,
    '&:first-line': {
      textTransform: "uppercase",
    }
  },
  code: {
    height: "480px",
    fontFamily: "Consolas, monospace",
    fontWeight: 600,
    // overflowX: "scroll",
    // whiteSpace: "nowrap",
    // maxWidth: "100%",
    width: "100%",
    position: "absolute",
    top: "48px", // space for tabs
    left: 0,
    margin: 0,
  },
  watermarkParent: {
    position: "relative",
    height: "528px"
  },
  blueprint: {
    height: "480px",
    width: "100%",
    position: "absolute",
    top: "48px", // space for tabs
    border: "none",
  },
  screenshot: {
    width: "100%",
    maxWidth: "100%",
    height: "auto"
  },
  video: {
    width: "100%",
    maxWidth: "100%",
    height: "auto"
  },
  leftColumn: {
    width: "100%"
  },
  cardButtons: {
    flexWrap: "wrap"
  },
  tab: {
    textTransform: "none",
    maxWidth: "2137px", // big number to fit long text on single tab
  },
  tabs: {
    backgroundColor: "#012C4D",
    width: "100%",
    position: "absolute",
    top: 0,
    left: 0,
  },
  tabsParent: {
    width: "100%",
    position: "relative",
    zIndex: 99, // prevent going on top of title bar
  }
}));

// For React Tabs ** START

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (<div>{children}</div>)}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `mymediatab-tab-${index}`,
    'aria-controls': `mymediatab-tabpanel-${index}`,
  };
}

// For React Tabs ** END

function Project(props) {
  const classes = useStyles(props);
  const [expanded, setExpanded] = React.useState(props.expandByDefault !== undefined);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const [tab, setTab] = React.useState(0);

  const handleTabChange = (event, newValue) => {
    setTab(newValue);
  };

  const id = makeProjectId(props.projectName);
  const idDetails = `${id}Details`;

  const engine = (props.details && props.details.engine) || defaultData.engine;
  const language =
    (props.details && props.details.language) || defaultData.language;
  const sourceControl =
    (props.details && props.details.sourceControl) || defaultData.sourceControl;

  const addBottomMargin = {
    marginBottom: "16px",
  }

  const myButtons = (
    <React.Fragment>
      {props.presskitUrl ? (
        <Button
          size="large"
          startIcon={<NewspaperIcon/>}
          href={props.presskitUrl}
          target="_blank"
        >
          Press kit
        </Button>
      ) : (
        ""
      )}
      {props.itchUrl ? (
        <Button
          size="large"
          startIcon={<FontAwesomeIcon icon={faItchIo} />}
          href={props.itchUrl}
          target="_blank"
        >
          Download on itch.io
        </Button>
      ) : (
        ""
      )}
      {props.customUrl ? (
        <Button
          size="large"
          startIcon={<WebIcon/>}
          href={props.customUrl[1]}
          target="_blank"
        >
          {props.customUrl[0]}
        </Button>
      ) : (
        ""
      )}
    </React.Fragment>
  )

  return (
    <Card className={classes.cardRoot} id={id}>
      <CardHeader
        avatar={
          <Avatar
            aria-label="Game's icon"
            className={classes.avatar}
            src={props.iconSrc}
          >
            {id.replace(/([^A-Z])/g, "")}
          </Avatar>
        }
        title={props.projectName}
        subheader={props.subheader}
      />
      {props.codeSamples ?
      <Box textAlign="left" display={{ xs: 'block'}} style={{margin: "0 16px 10px 16px"}}>
        <Typography style={{color: "rgba(255, 255, 255, 0.7)"}}>
          Sample code:
        </Typography>
      </Box>
      : ""}
      {props.codeSamples ?
        <AppBar className={classes.tabsParent}>
          <Tabs
            value={tab}
            onChange={handleTabChange}
            variant="scrollable"
            scrollButtons="auto"
            className={classes.tabs}
            aria-label="tabs to switch between example code files"
          >
            {props.codeSamples.map((theSample, index) => (
              <Tab label={theSample[0]} {...a11yProps(index)} key={index} className={classes.tab}/>
            ))}
          </Tabs>
        </AppBar>
      : ""}
      {props.codeSamples ?
        <CardMedia className={classes.watermarkParent}>
          {props.codeSamples.map((theSample, index) => (
            <TabPanel value={tab} index={index} key={index}>
              {theSample[2] ?
                <iframe src={theSample[1]} scrolling="no" allowFullScreen className={classes.blueprint}></iframe>
              :
                <React.Fragment>
                  <Box display={{ xs: 'none', lg: 'block' }}>
                    <Typography variant="h4" align="right" className={classes.watermark}>
                      {props.codeSampleLanguage == "cpp" ? "C++" : "C#"} code sample<br/>{theSample[0]}
                    </Typography>
                  </Box>
                  <SyntaxHighlighter
                    className={classes.code}
                    language={props.codeSampleLanguage || "csharp"}
                    style={vs2015}
                    showLineNumbers
                  >
                    {theSample[1]}
                  </SyntaxHighlighter>
                </React.Fragment>
            }
            </TabPanel>
          ))}
        </CardMedia>
      : ""}
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          {props.description}
        </Typography>
      </CardContent>
      <CardActions className={classes.cardButtons}>
        <Button
          size="large"
          startIcon={<MoreHorizIcon/>}
          variant="outlined"
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded
          })}
          onClick={() => {
            handleExpandClick();
            if (!expanded) {
              scrollTo(idDetails, -135);
            }
          }}
          aria-expanded={expanded}
          aria-label="show additional info or hide it"
        >
          {expanded ? "Hide info" : "Show more info"}
        </Button>
        {myButtons}
      </CardActions>
      <span id={idDetails}></span>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Grid container spacing={3}>
            <Grid item xs={12} md={props.thinRightPane ? 9 : 7} container>
              <Typography component="div" className={classes.leftColumn}>
                {props.details ? (
                  <React.Fragment>
                    {/* Project */}
                    <Typography style={addBottomMargin} component="div" variant="h5">
                      Project
                    </Typography>
                    <Typography style={addBottomMargin} component="div">Engine: {engine}</Typography>
                    <Typography style={addBottomMargin} component="div">Language: {language}</Typography>
                    <Typography style={addBottomMargin} component="div">Source control: {sourceControl}</Typography>
                    <Divider className={classes.dividerMargin} />
                    {/* Team */}
                    <Typography style={addBottomMargin} component="div" variant="h5">
                      Team
                    </Typography>
                    {props.details.team.map((t, i) => (
                      <Typography style={addBottomMargin} component="div" key={i}>
                        {t}
                      </Typography>
                    ))}
                    {props.details.myResponsibilities ? (
                      <React.Fragment>
                        <Divider className={classes.dividerMargin} />
                        {/* What I did */}
                        <Typography style={addBottomMargin} component="div" variant="h5">
                          What I did
                        </Typography>
                        <Typography style={addBottomMargin} component="div">I was responsible for:</Typography>
                        <ul>
                          {props.details.myResponsibilities.map((r, i) => (
                            <li key={i}>
                              <Typography style={addBottomMargin} component="div">{r}</Typography>
                            </li>
                          ))}
                        </ul>
                      </React.Fragment>
                    ) : (
                      ""
                    )}
                    {props.details.techniquesEngine &&
                    props.details.techniquesLanguage ? (
                      <React.Fragment>
                        <Divider className={classes.dividerMargin} />
                        {/* Techniques used */}
                        <Typography style={addBottomMargin} component="div" variant="h5">
                          Techniques I used
                        </Typography>
                        <Typography style={addBottomMargin} component="div">{engine}:</Typography>
                        <ul>
                          {props.details.techniquesEngine.map((t, i) => (
                            <li key={i}>
                              <Typography style={addBottomMargin} component="div">{t}</Typography>
                            </li>
                          ))}
                        </ul>
                        <Typography style={addBottomMargin} component="div">{language.split(" ")[0]}:</Typography>
                        <ul>
                          {props.details.techniquesLanguage.map((t, i) => (
                            <li key={i}>
                              <Typography style={addBottomMargin} component="div">{t}</Typography>
                            </li>
                          ))}
                        </ul>
                      </React.Fragment>
                    ) : (
                      ""
                    )}
                  </React.Fragment>
                ) : (
                  ""
                )}
              </Typography>
            </Grid>
            <Grid item xs={12} md={props.thinRightPane ? 3 : 5}>
              {props.videos ? (props.videos.map(v =>
                <video controls key={v} className={classes.video} preload="metadata">
                  <source src={v}/>
                </video>
              )) : ""}
              {props.screenshots ? (props.screenshots.map(ss =>
                <img src={ss} key={ss} className={classes.screenshot} aria-label="Image of the project"/>
              )) : ""}
            </Grid>
          </Grid>
          <Grid container spacing={3}>
            <Grid item xs={12} container justify="flex-start">
              <Stack spacing={1} direction="row" className={classes.cardButtons}>
                <Button
                  size="large"
                  startIcon={<ExpandLessIcon/>}
                  variant="outlined"
                  className={clsx(classes.expand, {
                    [classes.expandOpen]: expanded
                  })}
                  onClick={() => {
                    handleExpandClick();
                    scrollTo(id);
                  }}
                  aria-expanded={expanded}
                  aria-label="hide additional info and scroll back to the top of this project"
                >
                  Hide &amp; back to top
                </Button>
                {myButtons}
              </Stack>
            </Grid>
          </Grid>
        </CardContent>
      </Collapse>
    </Card>
  );
}

export default Project;
