const CometGameProgressManager = `using System.Collections;
using System.Collections.Generic;
using UnityEngine;

public class GameProgressManager : MonoBehaviour
{
    [SerializeField] private int levelCount = 10;
    private bool[] passedLevels;

    private static GameProgressManager instance;
    public static GameProgressManager Instance
    {
        get
        {
            if (instance == null)
            {
                Debug.LogError("Instance of " + nameof(GameProgressManager) + " is null");
            }
            return instance;
        }
    }

    public static bool InstanceIsNull => instance == null;

    private void Awake()
    {
        // ****************
        // Singleton setup
        // ****************
        if (instance != null)
        {
            Destroy(gameObject);
            return;
        }
        instance = this;
        DontDestroyOnLoad(gameObject);

        // ***********************************
        // Filling the array of passed levels
        // ***********************************
        passedLevels = new bool[levelCount];
        for (int i = 0; i < levelCount; i++)
        {
            passedLevels[i] = GetLevelPassed(i + 1);
        }
    }

    /// <summary>
    /// Returns the highest passed level number, or 0 if no level has been passed.
    /// </summary>
    public int GetHighestPassedLevel()
    {
        for (int i = passedLevels.Length - 1; i >= 0; i--)
        {
            if (passedLevels[i])
                return i + 1; // levels are numbered starting from 1
        }
        return 0;
    }

    public void SetLevelPassed(bool passed, int levelNumber)
    {
        PlayerPrefsHelpers.SetBool(MakeKeyString(levelNumber), passed);
        PlayerPrefs.Save();
        passedLevels[levelNumber - 1] = passed;
    }

    public bool GetLevelPassed(int levelNumber)
    {
        string key = MakeKeyString(levelNumber);
        if (!PlayerPrefs.HasKey(key))
            return false;

        return PlayerPrefsHelpers.GetBool(key);
    }

    private string MakeKeyString(int levelNumber)
    {
        const string Level = "Level";
        const string Passed = "Passed";
        return Level + levelNumber + Passed;
    }
}`;
export default CometGameProgressManager;