const RustbanditsHP = `using UnityEngine;

namespace Battle
{
    class HP
    {
        public float BaseHp { get; private set; }
        private float hp;

        public HP(float baseHp)
        {
            BaseHp = baseHp;
            hp = baseHp;
        }

        public static HP operator +(HP a, float b)
        {
            a.hp = Mathf.Clamp(a.hp + b, 0, a.BaseHp);
            return a;
        }

        public static HP operator -(HP a, float b)
        {
            a.hp = Mathf.Clamp(a.hp - b, 0, a.BaseHp);
            return a;
        }

        public static implicit operator float(HP hp) => hp.hp;

        public void Set(float hp)
        {
            this.hp = Mathf.Clamp(hp, 0, BaseHp);
        }

        public void SetBase(float baseHp, bool scaleIfFull)
        {
            if (scaleIfFull && hp == BaseHp)
            {
                hp = baseHp;
            }
            BaseHp = baseHp;
        }
    }
}`;
export default RustbanditsHP;