import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: 16,
    margin: "70px 60px"
  }
}));

function About(props) {
  const classes = useStyles();
  const getHeight = () => props.windowInnerHeight - 105 + "px";

  return (
    <div style={{ minHeight: getHeight() }}>
      <Typography variant="h4" id="About" paragraph>
        About
      </Typography>
      <Paper className={classes.paper} elevation={8}>
        <Typography paragraph>
          Hi! I am a game developer who is skilled in using Unity and Unreal Engine 4/5, with numerous finished
          projects and a good deal of experience working as part of a small team. I hope to keep improving my skills
          and taking part in making awesome, engaging games!
        </Typography>
      </Paper>
      <Typography variant="h5" paragraph>
        Skills
      </Typography>
      <Grid container>
        <Grid item xs={12}>
          <Typography variant="h6" paragraph>
            Game engines
          </Typography>
          <Typography paragraph> Unity</Typography>
          <Typography paragraph> Unreal Engine</Typography>
          <Typography variant="h6" paragraph>
            Programming languages
          </Typography>
          <Typography paragraph> C#</Typography>
          <Typography paragraph> C++</Typography>
          <Typography paragraph> Blueprints Visual Scripting</Typography>
          <Typography paragraph> Python</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h6" paragraph>
            Languages
          </Typography>
          <Typography paragraph> Polish (native)</Typography>
          <Typography paragraph> English</Typography>
          <Typography paragraph> Japanese</Typography>
          <Typography paragraph> German</Typography>
          <Typography variant="h6" paragraph>
            Soft skills
          </Typography>
          <Typography paragraph> Teamwork</Typography>
          <Typography paragraph> Attention to detail</Typography>
          <Typography paragraph> Openness to feedback</Typography>
        </Grid>
      </Grid>
      <br />
    </div>
  );
}

export default About;
